import Card from 'react-bootstrap/Card'

const AccountCard = ({account}) => {
  const isMember = account.crypto && account.shares && account.options && account.forex
  return (
    <Card bg='primary' text='dark' className='shadow border-0 mt-4 mb-4'>
      <Card.Body className='p-0 text-center text-dark border-secondary border-top border-bottom'>
        <h3 className='mt-3 pl-3 pr-3 font-weight-light'>
          {account.name}
        </h3>
        <h5>
          {account.email}
        </h5>
        <Card.Footer className='border-top border-secondary'>
          <small className="text-dark text-muted mt-3">
            {account.admin && 'admin'}
            {account.admin && isMember && ', '}
            {isMember && 'member'}
            {!isMember && account.crypto && 'crypto'}
            {!isMember && account.crypto && account.shares && ', '}
            {!isMember && account.shares && 'shares'}
            {!isMember && account.shares && account.options && ', '}
            {!isMember && account.options && 'options'}
            {!isMember && account.options && account.forex && ', '}
            {!isMember && account.forex && 'forex'}
          </small><br />
          {/* {admin &&
            <Link to={'/alerts/' + alert._id}>
              <Button className='shadow text-dark mt-2 mb-2 ml-3'
                variant='secondary' onClick={showEditModal}>
                Edit Alert
                                </Button>
            </Link>
          } */}
        </Card.Footer>
      </Card.Body>
    </Card>
  )
}

export default AccountCard