import { useState, useEffect } from 'react'

import axios from 'axios'

import CreateAccountModal from './create-account-modal.component'
import CreateAccountCard from './create-account-card.component'
import AccountCard from './account-card.component'

import CardColumns from 'react-bootstrap/CardColumns'
import Container from 'react-bootstrap/Container'

const AccountsList = () => {
  const [modal, setModal] = useState(false)
  const [accounts, setAccounts] = useState([])

  const updateAccounts = () => {
    axios.get('https://alertigo-api.herokuapp.com/accounts')
      .then(response => {
        const newAccounts = response.data.reverse()
        if (accounts.length === newAccounts.length) {
          return
        }
        else setAccounts(response.data.reverse())
      })
      .catch((error) => {
        console.log(error);
      })
  }

  useEffect(() => {
    axios.get('https://alertigo-api.herokuapp.com/accounts')
      .then(response => {
        const newAccounts = response.data.reverse()
        if (accounts.length === newAccounts.length) {
          return
        }
        else setAccounts(response.data.reverse())
      })
      .catch((error) => {
        console.log(error);
      })
  }, [accounts])

  return (
    <Container className='mt-5 mb-4'>
      <CreateAccountModal showModal={modal} closeModal={() => {updateAccounts(); setModal(false)}} />
      <CreateAccountCard showModal={() => setModal(true)} />
      <CardColumns>
        {accounts.map(account => {
          return (
            <AccountCard account={account}/>
          )
          })}
      </CardColumns>
    </Container>
  )
}

export default AccountsList