import { useEffect, useState } from 'react';

import axios from 'axios';

//react bootstrap imports
import Accordion from "react-bootstrap/Accordion";
import CardColumns from 'react-bootstrap/CardColumns'
import Container from 'react-bootstrap/Container'

import NewAlertCard from './new-alert-card.component'
import CreateAlertModal from './create-alert-modal.component'
import EditAlertModal from './edit-alert-modal.component';
import AlertCard from './alert-card.component';
import AlertChartModal from './alert-chart-modal.component'

const AlertsList = ({ admin, alertType, userName }) => {
  const [alerts, setAlerts] = useState([])
  const [createModal, setCreateModal] = useState(false)
  const [editModal, setEditModal] = useState(false)
  const [chartModal, setChartModal] = useState(false)
  const [chartTicker, setChartTicker] = useState('')
  let alertKey = 0

  const updateAlerts = () => {
    axios.get('https://alertigo-api.herokuapp.com/' + alertType + 'Alerts')
      .then(response => {
        const newAlerts = response.data.reverse()
        if (alerts.length === newAlerts.length) {
          return
        }
        else setAlerts(response.data.reverse())
      })
      .catch((error) => {
        console.log(error);
      })
  }

  useEffect(() => {
    // updateAlerts()
    axios.get('https://alertigo-api.herokuapp.com/' + alertType + 'Alerts')
      .then(response => {
        const newAlerts = response.data.reverse()
        if (alerts.length === newAlerts.length) {
          return
        }
        else setAlerts(response.data.reverse())
      })
      .catch((error) => {
        console.log(error);
      })
  }, [alertType, alerts])

  return (
    <Container className='pb-4'>
      <CreateAlertModal showCreateModal={createModal}
        closeCreateModal={() => { setCreateModal(false); updateAlerts() }}
        userName={userName}
        alertType={alertType}
      />
      <EditAlertModal showEditModal={editModal}
        closeEditModal={() => { window.location.href = window.location.href.substring(0, window.location.href.length - 24); setEditModal(false); updateAlerts() }}
        userName={userName}
        alertType={alertType}
      />
      <AlertChartModal showModal={chartModal}
        closeModal={() => setChartModal(false)}
        symbol={chartTicker}
      />
      {admin &&
        <NewAlertCard showCreateModal={() => setCreateModal(true)} />
      }
      <Accordion>
        <CardColumns>
          {alerts.map(currentAlert => {
            alertKey++
            return (
              <AlertCard
                showEditModal={() => { setEditModal(true) }}
                showChartModal={() => setChartModal(true)}
                setTicker={(val) => setChartTicker(val)}
                alertType={alertType}
                alert={currentAlert}
                alertKey={alertKey}
                key={alertKey}
                admin={admin}
              />
            )
          })}
        </CardColumns>
      </Accordion>
    </Container>
  )
}

export default AlertsList