import AlertsList from "./alerts-list.component"

import Tabs from 'react-bootstrap/Tabs'
import Tab from 'react-bootstrap/Tab'

const AlertsWrapper = ({ crypto, shares, options, forex, admin, userName }) => {
  return (
    <Tabs className='shadow' variant='pills' justify transition={false}>
      {crypto &&
        <Tab eventKey='crypto' title='CRYPTO' tabClassName='m-4'>
          <AlertsList alertType='crypto' admin={admin} userName={userName} />
        </Tab>
      }
      {shares &&
        <Tab eventKey='shares' title='SHARES' tabClassName='m-4'>
          <AlertsList alertType='shares' admin={admin} userName={userName} />
        </Tab>
      }
      {options &&
        <Tab eventKey='options' title='OPTIONS' tabClassName='m-4'>
          <AlertsList alertType='options' admin={admin} userName={userName} />
        </Tab>
      }
      {forex &&
        <Tab eventKey='forex' title='FOREX' tabClassName='m-4'>
          <AlertsList alertType='forex' admin={admin} userName={userName} />
        </Tab>
      }
    </Tabs>
  )
}

export default AlertsWrapper